import React from 'react';
import QueueAnim from 'rc-queue-anim';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import SlideBottomDetail from './SlideBottomDetail';
import {reactLocalStorage} from 'reactjs-localstorage';
import {Redirect} from 'react-router-dom';

let page = "slide_view";
let page2 = "slide_new";
let role = reactLocalStorage.get('role');
let view = role.includes(page);
let add = role.includes(page2);

const Page = () => (
  view === false?
  <Redirect to="/admin" />:
  <section className="container-fluid container-mw-xl chapter">
    <QueueAnim type="bottom" className="ui-animate">
      <div>
        <article className="article">
          <h2 className="article-title pt-4 mb-3">Slide Bottom Page</h2>
          {add === true?
          <Button href="#/admin/slidebottomadd" variant="extendedFab" aria-label="delete" className="mt-3" >
              <AddIcon/>
              Add Slide Bottom
          </Button>
          :null
          }
          <div className="box box-default table-box table-responsive mdl-shadow--2dp mt-3">
          <table className="mdl-data-table">
              <thead>
              <tr>
                  <th className="mdl-data-table__cell--non-numeric">ID</th>
                  <th className="mdl-data-table__cell--non-numeric">Placement</th>
                  <th className="mdl-data-table__cell--non-numeric">Position</th>
                  <th className="mdl-data-table__cell--non-numeric">Link</th>
                  <th className="mdl-data-table__cell--non-numeric">Action</th>
              </tr>
              </thead>

              <SlideBottomDetail />
              
          </table>
          </div>
        </article>
      </div>
    </QueueAnim>
  </section>
);

export default Page;
