import React,{Component} from 'react';
import axios from 'axios';
import {reactLocalStorage} from 'reactjs-localstorage';
import BorderColor from '@material-ui/icons/BorderColor';
import Close from '@material-ui/icons/Close';
import SweetAlert from 'sweetalert2-react';

class SlideBottomDetail extends Component{
    constructor(props){
        super(props);
        this.state = {
            slide : [],
            show : false
        }

        this.loadHandler = this.loadHandler.bind(this);
        this.loadHandler();
    }

    loadHandler = () => {
        const payload = {
            "placement": "Bottom Slide"
        }
        let token= reactLocalStorage.get('auth');
        let AuthStr = "Bearer ".concat(token);
        axios.post('http://13.229.249.101/dashboard/api/slide/view', payload, { headers: { Authorization: AuthStr } })
        .then(response => {
            const datas = response.data;
            this.setState({
                slide : datas
            })
        })
        .catch((error) => {
            console.log('error ' + error);
        })
    }

    removeHandler = (id) => {
        const payload = {
            "id": id,
            "placement": "Bottom Slide"
        }
        let token= reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.post('http://13.229.249.101/dashboard/api/slide/delete', payload, { headers: { Authorization: AuthStr } })
        .then((response) => {
            this.setState({
                show : true
            });
            this.loadHandler();
        })
        .catch((error) => {
            console.log(error);
        });  
        
    }

    render(){
        let page = "slide_edit";
        let page2 = "slide_delete";
        let role = reactLocalStorage.get('role');
        let edit = role.includes(page);
        let hapus = role.includes(page2);
        return (
            <tbody>
            <SweetAlert
                show={this.state.show}
                title="Slide"
                text="Delete Slide Success"
                onConfirm={() => 
                    this.setState({ show: false })
                }
            />
            {
            this.state.slide.map((tag,i) => {
                return (
                    <tr key={i}>
                        <td className="mdl-data-table__cell--non-numeric">{tag.id}</td>
                        <td className="mdl-data-table__cell--non-numeric">{tag.placement}</td>
                        <td className="mdl-data-table__cell--non-numeric">{tag.position}</td>
                        <td className="mdl-data-table__cell--non-numeric">{tag.link}</td>
                        <td className="mdl-data-table__cell--non-numeric">
                            {edit === true ?<a href={'#/admin/slidebottomedit/'+tag.id} className="text-dark"><BorderColor/></a>:null} 
                            {hapus === true ?<a href="javascript:void(0)" onClick={()=> this.removeHandler(tag.id)} className="text-dark"><Close/></a>:null }
                        </td>
                    </tr>
                )
            })
            }
            </tbody>
        );
    }
}

export default SlideBottomDetail;